//Form Componenets, can use with/without labels, with/without placeholder
//valid/invalid feedback included
import { useState } from "react";
import { Eye, EyeSlash } from "react-bootstrap-icons";
import { LoadingSpinner } from "../utilities/Utilities";

export function Input(props) {
  let feedback;

  if (props.valid === "is-valid") {
    feedback = "valid-feedback";
  } else if (props.valid === "is-invalid") {
    feedback = "invalid-feedback";
  } else {
    feedback = "";
  }

  return (
    <div className={"mb-3 " + props.className}>
      <label htmlFor={props.name} className="form-label">
        {props.label}
      </label>
      <input
        className={"form-control " + props.valid}
        type={props.type}
        id={props.name}
        name={props.name}
        value={props.value}
        onChange={props.onChange}
        required={props.req}
        autoComplete={"off"}
        placeholder={props.placeholder}
        autoFocus={props.focus}
        minLength={props.min}
        maxLength={props.max}
        disabled={props.disabled}
      />
      {feedback && <div className={feedback}>{props.feedback}</div>}
    </div>
  );
}
Input.defaultProps = {
  type: "text",
  req: true,
  min: "3",
  max: "100",
  focus: false,
  disabled: false,
};

export function InputPassword(props) {
  const [type, setType] = useState("password");

  const clickHandler = () => {
    type === "password" ? setType("text") : setType("password");
  };

  let feedback;

  if (props.valid === "is-valid") {
    feedback = "valid-feedback";
  } else if (props.valid === "is-invalid") {
    feedback = "invalid-feedback";
  } else {
    feedback = "";
  }

  return (
    <div className={"mb-3 " + props.className}>
      <label htmlFor={props.name} className="form-label">
        {props.label}
      </label>
      <div className="input-group">
        <input
          className={"form-control " + props.valid}
          type={type}
          id={props.name}
          name={props.name}
          value={props.value}
          onChange={props.onChange}
          required={props.req}
          autoComplete={"off"}
          placeholder={props.placeholder}
          autoFocus={props.focus}
          minLength={props.min}
          maxLength={props.max}
          disabled={props.disabled}
        />
        <span className="input-group-text" onClick={clickHandler}>
          {type === "text" ? <Eye /> : <EyeSlash />}
        </span>
      </div>
      {feedback && <div className={feedback}>{props.feedback}</div>}
    </div>
  );
}
InputPassword.defaultProps = {
  req: true,
  min: "6",
  max: "255",
  focus: false,
  disabled: false,
};

export function InputSubmit(props) {
  return (
    <div
      className={
        "d-flex align-items-center justify-content-center mb-3 " +
        props.className
      }
    >
      {props.spinner ? (
        <div className="btn btn-primary">
          <span className="me-1">
            {props.spinning ? props.spinning : props.value}
          </span>
          <LoadingSpinner />
        </div>
      ) : (
        <input type="submit" className="btn btn-primary" value={props.value} />
      )}
    </div>
  );
}
InputSubmit.defaultProps = { value: "Submit" };

export function RememberMe(props) {
  return (
    <div className={"form-check mb-3 " + props.className}>
      <input
        className="form-check-input"
        type="checkbox"
        id="rememberPassword"
        onChange={props.onChange}
      />
      <label htmlFor="rememberPassword" className="form-check-label">
        Remember me
      </label>
    </div>
  );
}

export function InputGroup(props) {
  return (
    <div className={"input-group mb-3 " + props.className}>
      {props.input}
      <span className="input-group-text">{props.text}</span>
    </div>
  );
}

export function InputFree(props) {
  return (
    <input
      type={props.type}
      className={"form-control " + props.className}
      name={props.name}
      id={props.name}
      value={props.value}
      onChange={props.onChange}
      onKeyDown={props.onKeyDown}
      required={props.req}
      accept={props.accept}
      autoFocus={props.focus}
      autoComplete={"off"}
      placeholder={props.placeholder}
    />
  );
}
InputFree.defaultProps = { type: "text", req: true, focus: false };
//accept: "file_extension | audio/*, video/*, image/*"

