import { useState } from "react";
import { FormContainer } from "../../../components/layout/MyLayout";
import { useAuth } from "../profile/contextconfig/AuthContext";
import { Input, InputSubmit } from "../../../components/form/Form";
import {
  AlertDanger,
  AlertSuccess,
} from "../../../components/utilities/Utilities";
import { Link } from "react-router-dom";

function ForgotPassword() {
  const [email, setEmail] = useState("");
  const [feedback, setFeedback] = useState("");
  const [spinner, setSpinner] = useState(false);
  const { forgotPassword } = useAuth();

  async function submitHandler(e) {
    e.preventDefault();
    setSpinner(true);
    setFeedback("");
    let firebaseError = false;

    try {
      await forgotPassword(email);
    } catch (error) {
      firebaseError = true;
      setSpinner(false);

      if (error.code === "auth/user-not-found") {
        return setFeedback("There is no such user with '" + email + "'");
      } else if (error.code === "auth/network-request-failed") {
        return setFeedback("Please check your internet connection.");
      } else {
        return setFeedback("An unknown error occurs. Try again later.");
      }
    }

    if (!firebaseError) {
      setFeedback(
        "We have sent an email to '" + email + "' to reset password."
      );
      setSpinner(false);
    }
  }

  return (
    <FormContainer>
      <form onSubmit={submitHandler}>
        <h4 className="text-center mb-3">Reset Password</h4>

        {feedback &&
          (feedback.includes("We have sent an email") ? (
            <AlertSuccess>
              <div>
                {feedback + " "}
                <Link className="" to="/profile/login">
                  login?
                </Link>
              </div>
            </AlertSuccess>
          ) : (
            <AlertDanger>{feedback}</AlertDanger>
          ))}

        <Input
          label="Enter your account's email"
          value={email}
          type="email"
          name="email"
          valid={feedback.includes("user") && "is-invalid"}
          onChange={(e) => setEmail(e.target.value)}
        />
        <InputSubmit value="Reset" spinning="Resetting" spinner={spinner} />
      </form>
    </FormContainer>
  );
}

export default ForgotPassword;
