import { useEffect, useState } from "react";
import { Container } from "../../components/layout/Layout";
import { AddIconInRightLowerCorner } from "../../components/layout/MyLayout";
import { LoadingSpinnerCenter } from "../../components/utilities/Utilities";
import PostTitle from "../post/PostTitle";
import app from "../account/profile/contextconfig/Config";
import { getDatabase, ref, set, onValue } from "firebase/database";
import { useFavorite } from "../favorite/FavContext";
import { useDb } from "./database/DbContext";

function Home() {
  const { favorite, removeFavorite, addFavorite } = useFavorite();
  const { title, deletePost } = useDb();
  const db = getDatabase();

  const deleteHandler = async (id) => {
    await deletePost(id);
    const db = getDatabase();
    set(ref(db, "titles/" + id), null);
    set(ref(db, "main/" + id), null);
  };

  const addHandler = async (id, count) => {
    await set(ref(db, "titles/" + id + "/like"), { like: count + 1 }).then(
      () => {
        addFavorite(id);
      }
    );
  };

  const removeHandler = async (id, count) => {
    await set(ref(db, "titles/" + id + "/like"), { like: count - 1 }).then(
      () => {
        removeFavorite(id);
      }
    );
  };

  return (
    <div>
      <Container className="mt-2 mt-lg-3">
        {title ? (
          Object.keys(title).map((key) => (
            <PostTitle
              key={title[key].id}
              post={title[key]}
              onDelete={() => deleteHandler(title[key].id)}
              alreadyFav={favorite.includes(title[key].id)}
              onAdd={() => addHandler(title[key].id, title[key].like.like)}
              onRemove={() =>
                removeHandler(title[key].id, title[key].like.like)
              }
            />
          ))
        ) : (
          <LoadingSpinnerCenter className="" />
        )}
      </Container>
      <AddIconInRightLowerCorner url="/upload" />
    </div>
  );
}

export default Home;
