import { useState } from "react";
import { List, GearFill, HeartFill } from "react-bootstrap-icons";
import { Link } from "react-router-dom";
import {
  ZawgyiUnicodeConverter,
  ZUConverter,
} from "../../components/burmese converter/BurmeseConverter";
import { Container, OffcanvasControl } from "../../components/layout/Layout";
import { NaviBarTopRow, NaviItem } from "../../components/layout/NaviBar";
import { ThemeChangerIcon } from "../../components/theme/Theme";
import "../../mystyles/style.css";

function MainNav() {
  let size = 27;
  let theme = "text-light";
  const [catagory, setCatagory] = useState(false);

  return (
    <NaviBarTopRow bg={" bg-primary "} className="">
      <div className="col-6 col-md-8 d-flex align-items-center p-0">
        <OffcanvasControl
          className="d-lg-block d-none p-1 nav-item dim btn"
          title={"Menu"}
        >
          <List size={size + 9} className="text-light" />
        </OffcanvasControl>
        <div
          onClick={() => setCatagory(!catagory)}
          className="d-block border d-lg-none p-1 nav-item dim btn btn-sm"
          title={"Menu"}
        >
          <List size={size} className="text-light" />
        </div>
        <Link
          to="/"
          className="text-decoration-none text-light ps-lg-3 ps-1 flex-grow-1"
          title="Home"
        >
          <div className="h5 mb-0">Sir Giant Oath</div>
        </Link>
      </div>

      <div className="col-4 d-none d-lg-block">
        <div className="d-flex align-items-center justify-content-evenly">
          <ZawgyiUnicodeConverter className="bg-success rounded p-1 me-2 text-light" />
          <ThemeChangerIcon className="nav-item dim btn px-2" width={"31"} />

          {/* <NaviItem url="/" title="Home">
          <img src={icons["paper"]} style={{ width: "27px" }} alt="main" />
        </NaviItem> */}
          <NaviItem url="/favorites" className={theme} title="Favorites">
            <HeartFill size={size - 2} />
          </NaviItem>

          <NaviItem url="/settings" className={theme} title="Settings">
            <GearFill size={size} />
          </NaviItem>
        </div>
      </div>

      <div className="col-6 col-md-4 p-0 d-lg-none d-block">
        <div className="d-flex align-items-center justify-content-between">
          <ZUConverter className="bg-success rounded px-1 text-light" />
          <ThemeChangerIcon className="nav-item dim btn px-1" width={"26"} />

          {/* <NaviItem url="/" title="Home">
          <img src={icons["paper"]} style={{ width: "27px" }} alt="main" />
        </NaviItem> */}
          <NaviItem
            url="/favorites"
            className={theme + " px-2"}
            title="Favorites"
          >
            <HeartFill size={size - 7} />
          </NaviItem>

          <NaviItem
            url="/settings"
            className={theme + " px-2 me-1"}
            title="Settings"
          >
            <GearFill size={size} />
          </NaviItem>
        </div>
      </div>

      {catagory && (
        <Container className="flex-row d-lg-none flex-nowrap overflow-auto border-top border scrollbar-hidden">
          <Catagory url="/" str="All" col="2" />
          <Catagory url="/straight" str="Straight" col="2" />
          <Catagory url="/lesbian" str="Lesbian" col="2" />
          <Catagory url="/gay" str="Gay" col="2" />
          <Catagory url="/threesomeMale" str="Threesome 2 Men" col="4" />
          <Catagory url="/threesomeFemale" str="Threesome 2 Women" col="5" />
          <Catagory url="/group" str="Group Sex" col="3" />
          <Catagory url="/hotwife" str="Hotwife" col="2" />
          <Catagory url="/toys" str="Sex Toys" col="2" />
          <Catagory url="/anal" str="Anal Sex" col="2" />
          <Catagory url="/incest" str="Incest" col="2" />
          <Catagory url="/transgender" str="Transgender" col="3" />
          <Catagory url="/bdsm" str="BDSM" col="2" />
          <Catagory url="/cuckold" str="Cuckold" col="2" />
          <Catagory url="/rape" str="Rape" col="2" />
        </Container>
      )}
    </NaviBarTopRow>
  );
}

export default MainNav;

const Catagory = (props) => {
  return (
    <Link
      to={props.url}
      className={
        "text-decoration-none text-light dim border-end text-center p-1 col-" +
        props.col
      }
    >
      {props.str}
    </Link>
  );
};
