import { useState } from "react";
import {
  Straight,
  Lesbian,
  Gay,
  ThreesomeFemale,
  ThreesomeMale,
  Group,
  Incest,
  Toys,
  Anal,
  TransGender,
  HotWife,
  BDSM,
  Rape,
  Cuckold,
} from "./UploadComponents";
import app from "../account/profile/contextconfig/Config";
import { getDatabase, set, ref } from "firebase/database";
import { useHistory } from "react-router";
import { Burmese } from "../../components/burmese converter/BurmeseConverter";
import { Input, InputArea, InputSubmit } from "../../components/form/Form";
import { FullScreenArrowGoBack } from "../../components/layout/MyLayout";
import { AlertWarning, ToastAlert } from "../../components/utilities/Utilities";
import { useAuth } from "../account/profile/contextconfig/AuthContext";

const initialValue = {
  title: "",
  content: "",
  type: "",
  originalWriter: "",
  mp3: "",
};

function Upload() {
  const [article, setArticle] = useState(initialValue);
  const [spinner, setSpinner] = useState(false);
  const [feedback, setFeedback] = useState("");
  const { currentUser } = useAuth();
  const history = useHistory();
  const db = getDatabase();

  async function submitHandler(e) {
    e.preventDefault();
    setSpinner(true);
    setFeedback("");

    if (article.type === "") {
      setSpinner(false);
      return setFeedback("   ");
    }

    let date = new Date();
    let id = date.getTime();

    const titles = {
      title: article.title,
      like: { like: 0 },
      id: id,
      type: article.type,
    };

    const main = {
      title: article.title,
      content: article.content,
      originalWriter: article.originalWriter,
      mp3: article.mp3,
    };

    try {
      set(ref(db, "main/" + id), main);
      set(ref(db, "titles/" + id), titles);
    } catch (error) {
      setSpinner(false);
      setFeedback("An error occurred. Please try again.");
      return console.log(error.code);
    }
    setSpinner(false);
    history.goBack();
  }

  const changeHandler = (e) => {
    let name = e.target.name;
    if (e.target.checked) {
      setFeedback("");
      if (article.type.includes(name)) {
        return;
      } else {
        setArticle({ ...article, type: article.type + name + ", " });
      }
    } else {
      setArticle({ ...article, type: article.type.replace(name + ", ", "") });
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setArticle({ ...article, [name]: value });
  };

  return (
    <FullScreenArrowGoBack
      prompt={article !== initialValue}
      mode={true}
      zu={true}
    >
      <form className="" onSubmit={submitHandler}>
        <AlertWarning>
          <small>
            <Burmese
              str={
                "        " +
                " mp3     "
              }
            />
          </small>
        </AlertWarning>
        <Input
          label={<Burmese str="" />}
          name="title"
          value={article.title}
          onChange={handleChange}
        />
        <label className="form-label" htmlFor="content">
          <Burmese str=" (  )" />
        </label>
        <div className="form-floating text-dark mb-3">
          <textarea
            name="content"
            maxLength="1000000"
            className="form-control"
            value={article.content}
            onChange={handleChange}
            style={{ height: "269px" }}
            id="content"
          />
          <label htmlFor="content text-dark">
            {article.content.length}/1000000
          </label>
        </div>

        {/* <InputArea
          rows="10"
          label={
            <Burmese str=" (  )" />
          }
          name="content"
          placeholder={"Maximum Character 1,000,000"}
          max="1000000"
          value={article.content}
          onChange={handleChange}
        /> */}
        <Input
          label={<Burmese str=" " />}
          name="originalWriter"
          value={article.originalWriter}
          onChange={handleChange}
        />

        {currentUser && currentUser.email === "whitedark@gmail.com" && (
          <Input
            req={false}
            label="mp3"
            name="mp3"
            value={article.mp3}
            onChange={handleChange}
          />
        )}

        {/* <Input
          type="file"
          name="image"
          onChange={fileUpload}
          label={<Burmese str="" />}
        /> */}

        <h6>
          <Burmese str=" ( )" />
        </h6>

        <div className="flex-column flex-md-row d-flex flex-wrap mb-3">
          <Straight onChange={changeHandler} />
          <Lesbian onChange={changeHandler} />
          <Gay onChange={changeHandler} />
          <ThreesomeFemale onChange={changeHandler} />
          <ThreesomeMale onChange={changeHandler} />
          <Group onChange={changeHandler} />
          <Incest onChange={changeHandler} />
          <Toys onChange={changeHandler} />
          <Anal onChange={changeHandler} />
          <TransGender onChange={changeHandler} />
          <HotWife onChange={changeHandler} />
          <Cuckold onChange={changeHandler} />
          <BDSM onChange={changeHandler} />
          <Rape onChange={changeHandler} />
        </div>
        <InputSubmit value="Upload" spinner={spinner} spinning={"Uploading"} />
      </form>
      {feedback && <ToastAlert str={feedback} />}
    </FullScreenArrowGoBack>
  );
}

export default Upload;
