import { useState } from "react";
import { useStyle } from "../context/StyleContext";
import { ThemeChangerIcon } from "../theme/Theme";
import { Container, DropDownMenu } from "./Layout";
import "../../mystyles/style.css";
import { Link } from "react-router-dom";
import { useEffect } from "react";

//Navigation Bar
export function NaviBar(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" ? "bg-dark " : props.bg;

  return (
    <nav className={"nav " + theme + props.className}>{props.children}</nav>
  );
}
NaviBar.defaultProps = { className: "nav-fill nav-pills", bg: "bg-light " };

export function NaviBarTopFlex(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" ? "bg-dark " : props.bg;
  const [top, setTop] = useState("0");
  const [y, setY] = useState(0);

  const scrollHandler = () => {
    const curY = window.scrollY;
    curY > y ? setTop("-169px") : setTop("0");
    setY(curY);
  };

  useEffect(() => {
    window.addEventListener("scroll", scrollHandler);
    return () => window.removeEventListener("scroll", scrollHandler);
  }, [top, y]);

  return (
    <nav
      className={
        "nav position-sticky start-0 end-0 align-items-center " +
        theme +
        props.className
      }
      style={{ zIndex: "9990", top: top, transition: "0.5s top" }}
    >
      {props.children}
    </nav>
  );
}
NaviBarTopFlex.defaultProps = {
  className: "nav-fill nav-pills",
  bg: "bg-light",
};

export function NaviBarTopRow(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" ? "bg-dark " : props.bg;
  const [top, setTop] = useState("0");
  const [y, setY] = useState(0);

  const scrollHandler = () => {
    const curY = window.scrollY;
    curY > y ? setTop("-169px") : setTop("0");
    setY(curY);
  };

  useEffect(() => {
    window.addEventListener("scroll", scrollHandler);
    return () => window.removeEventListener("scroll", scrollHandler);
  }, [top, y]);

  return (
    <div
      className={"position-sticky start-0 end-0  " + theme + props.className}
      style={{ zIndex: "9990", top: top, transition: "0.5s top" }}
    >
      <Container>{props.children}</Container>
    </div>
  );
}
NaviBarTopRow.defaultProps = {
  bg: "bg-light",
};

export function NaviItem(props) {
  return (
    <Link
      to={props.url}
      className={"nav-item dim"}
      style={{ cursor: "pointer" }}
      title={props.title}
    >
      <div className={"nav-link " + props.className}>{props.children}</div>
    </Link>
  );
}

export function NaviBarCollapse(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark" ? "navbar-dark bg-dark" : "navbar-light bg-light";

  return (
    <nav className={"navbar navbar-expand-lg " + theme}>
      <div className="container-fluid">
        {props.brand}
        <h2
          className="d-flex ms-3 mt-2 position-absolute top-0"
          style={{ right: "96px" }}
        >
          <ThemeChangerIcon />
        </h2>

        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target={"#navbarContent"}
          aria-controls={"navbarContent"}
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id={"navbarContent"}>
          {props.children}
        </div>
      </div>
    </nav>
  );
}

export function NaviBrand(props) {
  return (
    <span
      style={{ cursor: "pointer" }}
      className={"navbar-brand " + props.className}
    >
      {props.children}
    </span>
  );
}

export function NaviContent(props) {
  return <ul className="navbar-nav me-auto mb-2 mb-lg-0">{props.children}</ul>;
}

export function NaviSearch(props) {
  return (
    <form className="d-flex" onSubmit={props.onSubmit}>
      <input
        className="form-control me-2"
        type="search"
        placeholder="Search"
        aria-label="Search"
        value={props.value}
        onChange={props.onChange}
        autoComplete={"off"}
      />
      <button className="btn btn-outline-success" type="submit">
        Search
      </button>
    </form>
  );
}

export function NaviDropDown(props) {
  return (
    <li className="nav-item dropdown" style={{ cursor: "pointer" }}>
      <div
        className="nav-link dropdown-toggle"
        id={"navbarDropdown" + props.id}
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        {props.title}
      </div>
      <DropDownMenu>{props.children}</DropDownMenu>
    </li>
  );
}
