import { PersonBoundingBox } from "react-bootstrap-icons";
import { Container } from "../../../components/layout/Layout";
import { Block, FlexBox } from "../../../components/layout/MyLayout";
import { useAuth } from "./contextconfig/AuthContext";

function UserProfile() {
  const { currentUser, logout } = useAuth();

  async function logoutHandler() {
    try {
      logout();
    } catch (error) {
      console.log(error.code);
    }
  }

  return (
    <div className="p-2">
      <Container>
        <div className="col-lg-6">
          <Block className="rounded p-2 px-3 mb-3">
            <FlexBox className=" justify-content-between mb-3">
              <FlexBox>
                <PersonBoundingBox className="me-3" size="21" />
                <p className="text-break mb-0">
                  {currentUser && currentUser.email}
                </p>
              </FlexBox>
              <button
                className="btn btn-danger btn-sm"
                type="button"
                style={{ minWidth: "69px" }}
                onClick={logoutHandler}
              >
                Log out
              </button>
            </FlexBox>
          </Block>
        </div>
      </Container>
    </div>
  );
}

export default UserProfile;
