import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import {
  Input,
  InputPassword,
  InputSubmit,
  RememberMe,
} from "../../../components/form/Form";
import { FormContainer } from "../../../components/layout/MyLayout";
import { AlertDanger } from "../../../components/utilities/Utilities";
import { useAuth } from "../profile/contextconfig/AuthContext";

function SignUp() {
  const [account, setAccount] = useState({
    email: "",
    password: "",
    confirm: "",
  });
  const [feedback, setFeedback] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [spinner, setSpinner] = useState(false);
  const history = useHistory();
  const { signup } = useAuth();

  const changeHandler = (e) => {
    const { name, value } = e.target;
    setAccount({ ...account, [name]: value });
  };

  async function submitHandler(e) {
    e.preventDefault();
    setFeedback("");
    setSpinner(true);

    //check passwords match
    if (account.password !== account.confirm) {
      setSpinner(false);
      return setFeedback("Two Passwords do not match.");
    } else if (account.password.includes(" ")) {
      setSpinner(false);
      return setFeedback("Passwords cannot have space.");
    }

    let firebaseError = false;
    try {
      await signup(account.email, account.password);
    } catch (error) {
      firebaseError = true;
      setSpinner(false);

      if (error.code === "auth/email-already-in-use") {
        return setFeedback("Email already in use.");
      } else if (error.code === "auth/network-request-failed") {
        return setFeedback("Please check your internet connection.");
      } else {
        return setFeedback("An unknown error occured. Try again later.");
      }
    }

    if (!firebaseError) {
      console.log("Signup Success");

      if (rememberMe) {
        localStorage.setItem(
          "account",
          JSON.stringify({ email: account.email, password: account.password })
        );
      }
      history.push("/profile");
    }
  }

  return (
    <FormContainer>
      <form onSubmit={submitHandler}>
        <h4 className="text-center mb-3">Sign Up</h4>

        {feedback && <AlertDanger>{feedback}</AlertDanger>}

        <Input
          label="Email"
          value={account.email}
          type="email"
          name="email"
          valid={feedback.includes("Email") && "is-invalid"}
          onChange={changeHandler}
        />
        <InputPassword
          label="Password"
          value={account.password}
          name="password"
          onChange={changeHandler}
          valid={feedback.includes("Password") && "is-invalid"}
        />
        <InputPassword
          label="Confirm Password"
          value={account.confirm}
          name="confirm"
          onChange={changeHandler}
          valid={feedback.includes("Password") && "is-invalid"}
        />
        <div className="d-flex justify-content-between align-items-center mb-2">
          <RememberMe onChange={(e) => setRememberMe(e.target.checked)} />
          <div className="mb-3">
            Have an account? <Link to="/profile/login">Login</Link>
          </div>
        </div>
        <InputSubmit
          value="Sign Up"
          spinning="Creating account"
          spinner={spinner}
        />
      </form>
    </FormContainer>
  );
}
export default SignUp;
