import { getDatabase, onValue, ref } from "@firebase/database";
import app from "../../account/profile/contextconfig/Config";
import { useEffect, useState } from "react";
import { createContext, useContext } from "react";

const dbContext = createContext();

export function useDb() {
  return useContext(dbContext);
}

export function DbContextProvider(props) {
  const [data, setData] = useState();
  const [title, setTitle] = useState();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    let mount = true;
    const fetchTitle = async () => {
      const db = getDatabase();
      onValue(ref(db, "titles"), (snapshot) => {
        const da = snapshot.val();
        if (mount) {
          setTitle(da);
        }
      });
    };
    const fetchData = async () => {
      const db = getDatabase();
      onValue(ref(db, "main"), (snapshot) => {
        const da = snapshot.val();
        if (mount) {
          setData(da);
        }
      });
    };
    fetchTitle().then(() => {
      fetchData();
    });
    setLoading(false);
    return () => (mount = false);
  }, []);

  async function deletePost(id) {
    return setData(Object.keys(data).filter((key) => data[key] !== id));
  }

  const value = {
    data,
    title,
    loading,
    deletePost,
  };

  return (
    <dbContext.Provider value={value}>{props.children}</dbContext.Provider>
  );
}
