import { useStyle } from "../context/StyleContext";

//Accordion
export function Accordion(props) {
  const { style } = useStyle();

  return (
    <div
      className={
        "accordion-item " + (style.theme === "dark" && "bg-dark text-light")
      }
    >
      <h2 className="accordion-header" id={"heading" + props.id}>
        <button
          className={"accordion-button p-2 "}
          type="button"
          data-bs-toggle="collapse"
          data-bs-target={"#" + props.id}
          aria-expanded="true"
          aria-controls="collapseOne"
        >
          {props.title}
        </button>
      </h2>
      <div
        id={props.id}
        className={"accordion-collapse collapse " + props.display}
        aria-labelledby={props.id}
      >
        <div
          className={"accordion-body " + (style.theme === "dark" && "border")}
        >
          {props.children}
        </div>
      </div>
    </div>
  );
}
Accordion.defaultProps = { display: "show" };

//ButtonGroup
export function ButtGroup(props) {
  return (
    <div className={props.className} role={"group"}>
      {props.children}
    </div>
  );
}
ButtGroup.defaultProps = { className: "btn-group" };

//SlideShow
export function SlideShow(props) {
  return (
    <div
      id={"slideshow" + props.id}
      className="carousel slide"
      data-bs-ride="carousel"
    >
      {props.indicator}
      <div className="carousel-inner">{props.children}</div>
      <button
        className="carousel-control-prev"
        type="button"
        data-bs-target={"#slideshow" + props.id}
        data-bs-slide="prev"
      >
        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Previous</span>
      </button>
      <button
        className="carousel-control-next"
        type="button"
        data-bs-slide="next"
      >
        <span className="carousel-control-next-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Next</span>
      </button>
    </div>
  );
}
export function SlideShowImg(props) {
  return (
    <div
      className={"carousel-item " + props.active}
      data-bs-interval={props.interval}
    >
      <img
        src={props.src}
        className="d-block w-100"
        alt={props.alt}
        title={props.title ? props.title : props.alt}
      />
    </div>
  );
}
SlideShowImg.defaultProps = { interval: "2000" };
export function SlideShowIndicatorContainer(props) {
  return <div className="carousel-indicators">{props.children}</div>;
}
export function SlideShowIndicator(props) {
  return (
    <button
      type="button"
      data-bs-target={"#slideshow" + props.id}
      data-bs-slide-to={props.to}
      className={props.active}
      aria-current={props.active === "active" && "true"}
      aria-label={props.label}
    ></button>
  );
}

//Collapse
export function Collapse(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark" ? "bg-dark text-light " : "bg-light text-dark";

  return (
    <div className={"collapse " + props.className} id={"collapse" + props.id}>
      <div className={"card card-body " + theme}>{props.children}</div>
    </div>
  );
}
export function CollapseControl(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" ? "text-light " : "text-dark ";

  return (
    <div
      title={props.title}
      className={"btn " + theme + props.className}
      data-bs-toggle="collapse"
      data-bs-target={"#collapse" + props.id}
      aria-expanded="false"
      aria-controls={props.id}
    >
      {props.children}
    </div>
  );
}
CollapseControl.defaultProps = { className: " btn-primary" };

//Dropdown
export function DropDown(props) {
  return (
    <div className={props.className}>
      <button
        className={"btn dropdown-toggle " + props.btn}
        type="button"
        id={props.id}
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        {props.title}
      </button>
      {props.children}
    </div>
  );
}
DropDown.defaultProps = {
  btn: "btn-secondary",
  className: "dropdown ",
  id: "dropdown",
};
export function DropDownMenu(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" && "dropdown-menu-dark";

  return (
    <ul className={"dropdown-menu " + theme} aria-labelledby={props.id}>
      {props.children}
    </ul>
  );
}
DropDownMenu.defaultProps = { id: "dropdown" };
export function DropDownItem(props) {
  return (
    <li>
      <div className="dropdown-item">{props.children}</div>
    </li>
  );
}
export function DropDownDivider() {
  return (
    <li>
      <hr className="dropdown-divider" />
    </li>
  );
}

//ListGroup
export function ListGroup(props) {
  return <ul className={"list-group " + props.className}>{props.children}</ul>;
}
export function ListItem(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" && "bg-dark text-light ";

  return (
    <li
      className={
        "list-group-item list-group-item-action " + theme + props.className
      }
    >
      {props.children}
    </li>
  );
}

//Container-fluid Row
export function Container(props) {
  return (
    <div className="container-fluid">
      <div className={"row " + props.className}>{props.children}</div>
    </div>
  );
}

//Offcanvas
export function OffcanvasControl(props) {
  return (
    <div
      className={props.className}
      data-bs-toggle="offcanvas"
      data-bs-target={"#offcanvas" + props.id}
      aria-controls="offcanvas"
      title={props.title}
    >
      {props.children}
    </div>
  );
}
OffcanvasControl.defaultProps = { className: "btn btn-primary" };
export function Offcanvas(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" ? "bg-dark " : "";

  return (
    <div
      className={"offcanvas " + theme + props.className}
      style={{ zIndex: "9999" }}
      id={"offcanvas" + props.id}
      aria-labelledby="offcanvasLabel"
    >
      <div className="offcanvas-header">
        <h5 className="offcanvas-title" id="offcanvasLabel">
          {props.title}
        </h5>
        <button
          type="button"
          className={
            "btn-close text-reset " +
            (style.theme === "dark" && "btn-close-white")
          }
          data-bs-dismiss="offcanvas"
          aria-label="Close"
        ></button>
      </div>
      <div className="offcanvas-body">{props.children}</div>
    </div>
  );
}
Offcanvas.defaultProps = { className: " offcanvas-start" };
