import { useEffect, useState } from "react";
import { createContext, useContext } from "react";

const favContext = createContext();

export function useFavorite() {
  return useContext(favContext);
}

let n = JSON.parse(localStorage.getItem("favorite"));

const initialValue = n ? n : [];

export function FavContextProvider(props) {
  const [favorite, setFavorite] = useState(initialValue);

  useEffect(() => {
    localStorage.setItem("favorite", JSON.stringify(favorite));
  }, [favorite]);

  function removeFavorite(id) {
    return setFavorite(favorite.filter((item) => item !== id));
  }

  function addFavorite(id) {
    return setFavorite([...favorite, id]);
  }

  const value = { favorite, addFavorite, removeFavorite };

  return (
    <favContext.Provider value={value}>{props.children}</favContext.Provider>
  );
}
