import { useEffect } from "react";
import { useState } from "react";
import { Dash, Plus } from "react-bootstrap-icons";
import {
  Burmese,
  BurmeseFontConverter,
  ZUConverter,
} from "../burmese converter/BurmeseConverter";
import { useStyle } from "../context/StyleContext";
import { InputSwitch } from "../form/Form";
import { Container } from "../layout/Layout";
import { Card, TextWrap, ToastOnlyText } from "../utilities/Utilities";

export function FontSizeChanger(props) {
  const { style, changeStyle } = useStyle();
  const [toast, setToast] = useState("");

  const plusHandler = () => {
    style.fontSize < props.max && changeStyle("fontSize", style.fontSize + 1);
    style.fontSize === props.max &&
      setToast("Maximum font-size is " + props.max);
  };

  const minusHandler = () => {
    style.fontSize > props.min && changeStyle("fontSize", style.fontSize - 1);
    style.fontSize === props.min &&
      setToast("Minimum font-size is " + props.min);
  };

  useEffect(() => {
    const timeOut = setTimeout(() => {
      setToast("");
    }, 1569);
    return () => clearTimeout(timeOut);
  }, [toast]);

  return (
    <div>
      <PlusMinus
        str="Font Size"
        className={props.className}
        val={style.fontSize}
        onPlus={plusHandler}
        onMinus={minusHandler}
      />
      {toast && <ToastOnlyText str={toast} />}
    </div>
  );
}
FontSizeChanger.defaultProps = { max: 24, min: 12 };

export function LineHeightChanger(props) {
  const { style, changeStyle } = useStyle();
  const [toast, setToast] = useState("");

  const plusHandler = () => {
    style.lineHeight < props.max &&
      changeStyle("lineHeight", (style.lineHeight * 10 + 1) / 10);
    style.lineHeight === props.max &&
      setToast("Maximum line-height is " + props.max);
  };

  const minusHandler = () => {
    style.lineHeight > props.min &&
      changeStyle("lineHeight", (style.lineHeight * 10 - 1) / 10);
    style.lineHeight === props.min &&
      setToast("Minimum line-height is " + props.min);
  };

  useEffect(() => {
    const timeOut = setTimeout(() => {
      setToast("");
    }, 1569);
    return () => clearTimeout(timeOut);
  }, [toast]);

  return (
    <div>
      <PlusMinus
        str="Line Height"
        className={props.className}
        val={style.lineHeight}
        onPlus={plusHandler}
        onMinus={minusHandler}
      />
      {toast && <ToastOnlyText str={toast} />}
    </div>
  );
}
LineHeightChanger.defaultProps = { max: 2, min: 1 };

export function FontChanger(props) {
  return (
    <div className={"p-2 " + props.className}>
      <Container className="d-flex align-items-center">
        <div className="col-6">Burmese Font</div>
        <div className="col-3 text-center">
          <BurmeseFontConverter />
        </div>
        <div className="col-3 d-flex justify-content-center ">
          <ZUConverter />
        </div>
      </Container>
    </div>
  );
}

export function ThemeChanger(props) {
  const { changeStyle, style } = useStyle();

  const changeHandler = (e) => {
    if (e.target.checked) {
      changeStyle("theme", "dark");
    } else {
      changeStyle("theme", "light");
    }
  };

  return (
    <div className={"p-2 " + props.className}>
      <Container>
        <div className="col-6">
          <label htmlFor="themechangerSetting" style={{ cursor: "pointer" }}>
            Dark theme
          </label>
        </div>
        <div className="col-6 d-flex justify-content-center">
          <InputSwitch
            id="themechangerSetting"
            checked={style.theme === "dark" ? true : false}
            onChange={changeHandler}
          />
        </div>
      </Container>
    </div>
  );
}

export function PlusMinus(props) {
  return (
    <div className={"p-2 " + props.className}>
      <Container className="d-flex align-items-center">
        <div className="col-6">{props.str}</div>
        <div className="col-2 text-center">
          <button
            type="button"
            className="btn btn-primary btn-sm text-light"
            onClick={props.onMinus}
          >
            <Dash size={21} />
          </button>
        </div>
        <div className="col-2 text-center">{props.val}</div>
        <div className="col-2 text-center">
          <button
            type="button"
            className="btn btn-primary btn-sm text-light"
            onClick={props.onPlus}
          >
            <Plus size={21} />
          </button>
        </div>
      </Container>
    </div>
  );
}

export function TestBurmeseText() {
  let text =
    "       " +
    "       " +
    "        " +
    "       " +
    "  ttt  ";

  return (
    <TextWrap>
      <Burmese str={text.replace(/ttt/gi, "\t")} />
    </TextWrap>
  );
}
