import { Heart, HeartFill, Pencil, Trash } from "react-bootstrap-icons";
import { Link } from "react-router-dom";
import { Burmese } from "../../components/burmese converter/BurmeseConverter";
import { useStyle } from "../../components/context/StyleContext";
import { FlexBox } from "../../components/layout/MyLayout";
import { Card } from "../../components/utilities/Utilities";
import { icons } from "../../icons";
import { useAuth } from "../account/profile/contextconfig/AuthContext";
import { useFavorite } from "../favorite/FavContext";

function PostTitle(props) {
  const post = props.post;
  let type = post.type.split(", ");
  const { style } = useStyle();
  const { currentUser } = useAuth();
  const theme = style.theme === "dark" ? "text-light" : "text-dark";

  return (
    <div className="col-lg-6">
      <Card className="mb-2 p-2">
        <FlexBox className="justify-content-between">
          <Link
            title="Click to see full details"
            to={"/post?name=" + post.id}
            className="text-decoration-none flex-grow-1"
          >
            <h5 className="mb-3">
              <Burmese str={post.title} />
            </h5>
          </Link>
          {currentUser && currentUser.email === "whitedark@gmail.com" && (
            <FlexBox>
              <Link
                to={"edit?name=" + post.id}
                className="btn dim d-flex align-items-center"
                title="Edit"
              >
                <Pencil className={theme} />
              </Link>
              <div
                className="btn dim d-flex align-items-center"
                title="Delete"
                onClick={props.onDelete}
              >
                <Trash className={theme} />
              </div>
            </FlexBox>
          )}
        </FlexBox>

        <FlexBox className="justify-content-between">
          <Link
            title="Click to see full details"
            to={"/post?name=" + post.id}
            className="text-decoration-none flex-grow-1"
          >
            <FlexBox className="flex-wrap">
              {type.map((item) => insertIcon(item))}
            </FlexBox>
          </Link>

          {props.alreadyFav ? (
            <div
              style={{ minWidth: "69px" }}
              title="remove from favorite"
              className="text-center"
              onClick={props.onRemove}
            >
              <HeartFill className="text-danger" />
              {" " + post.like.like}
            </div>
          ) : (
            <div
              style={{ minWidth: "69px" }}
              title="add to favorite"
              className="text-center"
              onClick={props.onAdd}
            >
              <Heart />
              {" " + post.like.like}
            </div>
          )}
        </FlexBox>
      </Card>
    </div>
  );
}
export default PostTitle;

let width = "29px";

