import { useEffect } from "react";
import { useState } from "react";
import {
  CheckCircleFill,
  ExclamationTriangleFill,
} from "react-bootstrap-icons";
import { useStyle } from "../context/StyleContext";

//loading
export function LoadingSpinner(props) {
  const { style } = useStyle();
  let theme = style.theme === "dark" ? "text-light " : "text-dark ";
  return (
    <span
      className={"spinner-border " + theme + props.className}
      role="status"
      aria-hidden="true"
    ></span>
  );
}
LoadingSpinner.defaultProps = { className: " spinner-border-sm" };
export function LoadingSpinnerCenter(props) {
  return (
    <div className="position-fixed start-50 top-50 translate-middle text-center">
      <LoadingSpinner className={props.className} />
    </div>
  );
}

//alerts
export function AlertSuccess(props) {
  return (
    <div className="alert alert-success p-2 d-flex align-items-center">
      <CheckCircleFill className="me-2" />
      {props.children}
    </div>
  );
}

export function AlertDanger(props) {
  return (
    <div className="alert alert-danger p-2 d-flex align-items-center">
      <ExclamationTriangleFill className="me-2" />
      {props.children}
    </div>
  );
}

export function AlertWarning(props) {
  return (
    <div className="alert alert-warning p-2 d-flex align-items-center">
      <ExclamationTriangleFill className="me-2" />
      {props.children}
    </div>
  );
}

//badge
export function BadgeCircle() {
  return (
    <span
      className="position-absolute top-0 start-100 translate-middle p-2 bg-danger border border-light rounded-circle"
      style={{ zIndex: "9998" }}
    >
      <span className="visually-hidden">New Items</span>
    </span>
  );
}

export function BadgeMsg(props) {
  return (
    <span
      className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger"
      style={{ zIndex: "9998" }}
    >
      {props.msg}
      <span className="visually-hidden">unread messages</span>
    </span>
  );
}

//progress bar
export function ProgressBar(props) {
  return (
    <div className="progress">
      <div
        className="progress-bar"
        role="progressbar"
        style={{ width: props.width + "%" }}
        aria-valuenow={props.width}
        aria-valuemin="0"
        aria-valuemax="100"
      >
        {props.width}%
      </div>
    </div>
  );
}

//Toast Message
export function ToastOnlyText(props) {
  return (
    <div
      className={
        "position-fixed bottom-0 start-50 translate-middle mb-2 p-2 py-1 rounded-pill bg-light text-dark text-info"
      }
      style={{ zIndex: "9999" }}
    >
      {props.str}
    </div>
  );
}
export function ToastOnlyTextControl(props) {
  const [str, setStr] = useState("");

  useEffect(() => {
    const timeOut = setTimeout(() => {
      setStr("");
    }, 1569);
    return () => clearTimeout(timeOut);
  }, [str]);

  return (
    <div className={"btn " + props.className} onClick={() => setStr(props.str)}>
      {props.children}
      {str && <ToastOnlyText str={props.str} />}
    </div>
  );
}
export function ToastAlert(props) {
  return (
    <div
      className={
        "position-fixed bottom-0 start-50 translate-middle p-2 alert alert-danger w-75 text-center mb-0 "
      }
      style={{ zIndex: "9999" }}
    >
      {props.str}
    </div>
  );
}

//horizontal bar
export function Bar(props) {
  return (
    <hr
      className={"p-0 m-0 " + props.className}
      style={{ height: props.h + "px" }}
    />
  );
}

//Modal close/okay
export function Modal(props) {
  let mode = props.mode;
  const { style } = useStyle();

  let clr;
  switch (mode) {
    case "danger":
      clr = "text-danger";
      if (style.theme === "dark") {
        clr += " bg-dark";
      }
      break;
    case "warning":
      clr = "text-warning";
      if (style.theme === "dark") {
        clr += " bg-dark";
      }
      break;
    default:
      clr =
        style.theme === "dark" ? "bg-dark text-light" : "bg-light text-dark";
  }

  return (
    <div>
      <div
        className="position-fixed top-50 start-50 translate-middle w-100 px-3"
        style={{ maxWidth: "444px", zIndex: "9999" }}
        tabIndex="-1"
      >
        <div className={"border rounded " + props.className}>
          <div className={"modal-content " + clr}>
            <div className="modal-header">
              <h5 className={"modal-title"}>{props.title}</h5>
              <button
                type="button"
                className={
                  "btn-close " + (style.theme === "dark" && "btn-close-white")
                }
                onClick={props.onCancel}
                aria-label="Close"
              ></button>
            </div>
            {props.children}
          </div>
        </div>
      </div>
      <FadeOut onClick={props.onCancel} />
    </div>
  );
}
export function ModalBody(props) {
  return <div className="modal-body">{props.children}</div>;
}
export function ModalFooter(props) {
  return <div className="modal-footer">{props.children}</div>;
}

//FadeOut
export function FadeOut(props) {
  return (
    <div
      className="bg-dark opacity-25 position-fixed top-0 end-0 start-0 bottom-0"
      onClick={props.onClick}
    ></div>
  );
}

//Card
export function Card(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark"
      ? "bg-dark text-light "
      : "bg-light text-dark border-dark ";

  return (
    <div
      className={"card border " + theme + props.className}
      style={{ width: props.width }}
    >
      {props.children}
    </div>
  );
}
export function CardBody(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark" ? "bg-dark text-light " : "bg-light text-dark ";

  return (
    <div className={"card-body " + theme + props.className}>
      {props.children}
    </div>
  );
}
export function CardTitle(props) {
  return (
    <div className={"card-title " + props.className}>{props.children}</div>
  );
}

export function CardText(props) {
  return <div className={"card-text " + props.className}>{props.children}</div>;
}

//TextWraper
export function TextWrap(props) {
  const { style } = useStyle();

  return (
    <pre
      className={
        style.theme === "dark"
          ? "text-break w-100 text-light p-2"
          : "text-dark text-break p-2 w-100"
      }
      id={props.id}
      style={{
        whiteSpace: "pre-wrap",
        fontSize: style.fontSize + "px",
        lineHeight: style.lineHeight + "em",
      }}
    >
      {props.children}
    </pre>
  );
}
