import { createContext, useContext, useEffect, useState } from "react";

let n = JSON.parse(localStorage.getItem("style"));

const initialValue = n
  ? n
  : {
      font: "unicode",
      fontSize: 16,
      lineHeight: 1.5,
      theme: "dark",
    };

const styleContext = createContext();

export function useStyle() {
  return useContext(styleContext);
}

export function ContextProvider(props) {
  const [style, setStyle] = useState(initialValue);

  function changeStyle(name, value) {
    return setStyle({ ...style, [name]: value });
  }

  useEffect(() => {
    localStorage.setItem("style", JSON.stringify(style));
    style.theme === "dark" && document.body.classList.add("dark-theme");
    style.theme === "light" && document.body.classList.remove("dark-theme");
  }, [style]);

  return (
    <styleContext.Provider value={{ style, changeStyle }}>
      {props.children}
    </styleContext.Provider>
  );
}
