import { Route, Switch } from "react-router";
import Profile from "../account/profile/Profile";
import Favorite from "../favorite/Favorite";
import Home from "../home/Home";
import Settings from "../setting/Settings";
import Upload from "../upload/Upload";
import MainNav from "./MainNav";
import Menu from "../menu/Menu";
import FullPost from "../post/PostFull";
import Edit from "../post/edit/Edit";
import Catagory from "../menu/catagory/Catagory";

function Main() {
  return (
    <div>
      <Switch>
        <Route path="/" exact>
          <MainNav />
          <Home />
          <Menu />
        </Route>
        <Route path="/settings">
          <MainNav />
          <Settings />
          <Menu />
        </Route>
        <Route path="/favorites">
          <MainNav />
          <Favorite />
          <Menu />
        </Route>
        <Route path="/profile">
          <MainNav />
          <Profile />
        </Route>
        <Route path="/upload">
          <Upload />
        </Route>
        <Route path="/post">
          <FullPost />
        </Route>
        <Route path="/edit">
          <Edit />
        </Route>
        <Route path="/straight">
          <MainNav />
          <Catagory type="Straight" />
          <Menu />
        </Route>
        <Route path="/lesbian">
          <MainNav />
          <Catagory type="Lesbian" />
          <Menu />
        </Route>
        <Route path="/gay">
          <MainNav />
          <Catagory type="Gay" />
          <Menu />
        </Route>
        <Route path="/threesomeMale">
          <MainNav />
          <Catagory type="ThreesomeMale" />
          <Menu />
        </Route>
        <Route path="/threesomeFemale">
          <MainNav />
          <Catagory type="ThreesomeFemale" />
          <Menu />
        </Route>
        <Route path="/group">
          <MainNav />
          <Catagory type="Group" />
          <Menu />
        </Route>
        <Route path="/incest">
          <MainNav />
          <Catagory type="Incest" />
          <Menu />
        </Route>
        <Route path="/anal">
          <MainNav />
          <Catagory type="Anal" />
          <Menu />
        </Route>
        <Route path="/toys">
          <MainNav />
          <Catagory type="Toys" />
          <Menu />
        </Route>
        <Route path="/bdsm">
          <MainNav />
          <Catagory type="BDSM" />
          <Menu />
        </Route>
        <Route path="/hotwife">
          <MainNav />
          <Catagory type="HotWife" />
          <Menu />
        </Route>
        <Route path="/cuckold">
          <MainNav />
          <Catagory type="Cuckold" />
          <Menu />
        </Route>
        <Route path="/rape">
          <MainNav />
          <Catagory type="Rape" />
          <Menu />
        </Route>
        <Route path="/transgender">
          <MainNav />
          <Catagory type="TransGender" />
          <Menu />
        </Route>
        <Route path="*">
          <MainNav />
          <Home />
          <Menu />
        </Route>
      </Switch>
    </div>
  );
}

export default Main;
