import { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import {
  Input,
  InputPassword,
  InputSubmit,
  RememberMe,
} from "../../../components/form/Form";
import { FormContainer } from "../../../components/layout/MyLayout";
import { AlertDanger } from "../../../components/utilities/Utilities";
import { useAuth } from "../profile/contextconfig/AuthContext";

function Login() {
  const [account, setAccount] = useState({
    email: "",
    password: "",
  });
  const [feedback, setFeedback] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [spinner, setSpinner] = useState(false);
  const { login } = useAuth();
  const history = useHistory();

  useEffect(() => {
    let n = JSON.parse(localStorage.getItem("account"));
    if (n) {
      n.email && n.password && setAccount(n);
      setRememberMe(true);
    }
  }, []);

  const changeHandler = (e) => {
    const { name, value } = e.target;
    setAccount({ ...account, [name]: value });
  };

  async function submitHandler(e) {
    e.preventDefault();
    setFeedback("");
    setSpinner(true);

    let firebaseError = false;

    try {
      await login(account.email, account.password);
    } catch (error) {
      firebaseError = true;
      setSpinner(false);

      if (error.code === "auth/user-not-found") {
        return setFeedback("There is no such user. Check your Email.");
      } else if (error.code === "auth/wrong-password") {
        return setFeedback("Wrong Password.");
      } else if (error.code === "auth/network-request-failed") {
        return setFeedback("Please check your internet connection.");
      } else {
        return setFeedback("An unknown error occured. Try again later.");
      }
    }

    if (!firebaseError) {
      console.log("Login Success");

      if (rememberMe) {
        localStorage.setItem(
          "account",
          JSON.stringify({ email: account.email, password: account.password })
        );
      } else {
        localStorage.removeItem("account");
      }

      history.push("/profile");
    }
  }

  return (
    <FormContainer>
      <form onSubmit={submitHandler} className="pb-2">
        <h4 className="text-center mb-3">Log In</h4>

        {feedback && <AlertDanger>{feedback}</AlertDanger>}

        <Input
          label="Email"
          value={account.email}
          type="email"
          name="email"
          valid={feedback.includes("Email") && "is-invalid"}
          onChange={changeHandler}
        />
        <InputPassword
          label="Password"
          value={account.password}
          name="password"
          onChange={changeHandler}
          valid={feedback.includes("Password") && "is-invalid"}
        />

        <div className="d-flex justify-content-between align-items-center">
          <RememberMe onChange={(e) => setRememberMe(e.target.checked)} />
          <div className="mb-3">
            <Link to="/profile/forgotPassword">forgot password</Link>
          </div>
        </div>

        <InputSubmit value="Log In" spinning="Loggin In" spinner={spinner} />
      </form>

      <div className="text-center mt-5 mb-3">
        Have no account? <Link to="/profile/signup">Signup</Link>
      </div>
    </FormContainer>
  );
}
export default Login;
