import { Route, Switch } from "react-router";
import { FullScreenArrowGoBack } from "../../../components/layout/MyLayout";
import { LoginSignup } from "../../main/SmallComponents";
import ForgotPassword from "../accountSetting/ForgotPassword";
import Login from "../login/Login";
import SignUp from "../signup/SignUp";
import { useAuth } from "./contextconfig/AuthContext";
import UserProfile from "./UserProfile";

function Profile() {
  const { currentUser } = useAuth();

  return (
    <div>
      <Switch>
        <Route path="/profile" exact>
          {currentUser ? <UserProfile /> : <LoginSignup />}
        </Route>

        <Route path="/profile/login">
          <FullScreenArrowGoBack location="/profile">
            <Login />
          </FullScreenArrowGoBack>
        </Route>
        <Route path="/profile/signup">
          <FullScreenArrowGoBack location="/profile">
            <SignUp />
          </FullScreenArrowGoBack>
        </Route>
        <Route path="/profile/forgotPassword">
          <FullScreenArrowGoBack>
            <ForgotPassword />
          </FullScreenArrowGoBack>
        </Route>
      </Switch>
    </div>
  );
}
export default Profile;
