import { useState } from "react";
import { Link } from "react-router-dom";
import { InputFree } from "../../components/form/Form";
import {
  AlertDanger,
  Card,
  CardBody,
  FadeOut,
  Modal,
  ModalBody,
  ModalFooter,
} from "../../components/utilities/Utilities";

//For Profile Forms
export function LoginSignup() {
  const [value, setValue] = useState("");
  const [correct, setCorrect] = useState(false);
  const [feedback, setFeedback] = useState("");

  const submitHandler = () => {
    setFeedback("");
    if (value === "Pand0raliger0") {
      setCorrect(true);
    } else {
      return setFeedback("Wrong Password");
    }
  };
  return (
    <div>
      <h2 className="text-center m-3 mt-5">Welcome From Sir Oath</h2>

      {!correct && (
        <div>
          <FadeOut />
          <Modal title={"Enter Password"}>
            <ModalBody>
              {feedback && <AlertDanger>{feedback}</AlertDanger>}

              <InputFree
                value={value}
                focus={true}
                placeholder={"Password"}
                onKeyDown={(e) => e.keyCode === 13 && submitHandler()}
                onChange={(e) => setValue(e.target.value)}
              />
            </ModalBody>
            <ModalFooter>
              <button className="btn btn-primary" onClick={submitHandler}>
                Go
              </button>
            </ModalFooter>
          </Modal>
        </div>
      )}

      <Card className="w-lg-50 w-75 m-auto">
        <CardBody>
          <Link to="/profile/login" className="d-block btn btn-primary">
            Login
          </Link>
          <div className="text-center m-3">or</div>
          <Link to="/profile/signup" className="d-block btn btn-danger">
            Signup
          </Link>
        </CardBody>
      </Card>
    </div>
  );
}
