import { useStyle } from "../context/StyleContext";
import { InputCheckSwitch } from "../form/Form";
import "../../mystyles/style.css";
import { icons } from "../../icons";

export function ThemeChangerSwitch() {
  const { changeStyle, style } = useStyle();

  const changeHandler = (e) => {
    if (e.target.checked) {
      changeStyle("theme", "dark");
    } else {
      changeStyle("theme", "light");
    }
  };

  return (
    <div className="mt-2">
      <InputCheckSwitch
        onChange={changeHandler}
        name="theme"
        checked={style.theme === "dark" ? true : false}
        label={"dark mode"}
      />
    </div>
  );
}

export function ThemeChangerIcon(props) {
  const { changeStyle, style } = useStyle();

  const changeHandler = () => {
    if (style.theme !== "dark") {
      changeStyle("theme", "dark");
    } else {
      changeStyle("theme", "light");
    }
  };

  return (
    <div className={props.className} onClick={changeHandler}>
      <img
        src={icons["dark_light"]}
        alt="light"
        title="change theme"
        style={{ width: props.width + "px" }}
      />
    </div>
  );
}
ThemeChangerIcon.defaultProps = { width: "35" };
