import { useEffect, useState } from "react";
import { Fullscreen, FullscreenExit, HeartFill } from "react-bootstrap-icons";
import { Burmese } from "../../components/burmese converter/BurmeseConverter";
import { useStyle } from "../../components/context/StyleContext";
import { Collapse, CollapseControl } from "../../components/layout/Layout";
import { FlexBox } from "../../components/layout/MyLayout";
import {
  Card,
  CardTitle,
  TextWrap,
  ToastOnlyText,
} from "../../components/utilities/Utilities";
import { icons } from "../../icons";

function FavoritePost(props) {
  const post = props.post;
  const { style } = useStyle();
  const [toast, setToast] = useState("");

  const copyHandler = () => {
    const copy = document.getElementById("content" + props.id);
    navigator.clipboard.writeText(copy.innerHTML);
    setToast("Copied");
  };

  useEffect(() => {
    const timeOut = setTimeout(() => setToast(""), 1569);
    return () => clearTimeout(timeOut);
  }, [toast]);

  return (
    <Card className="mb-2 mb-lg-3">
      <CardTitle>
        <FlexBox className="justify-content-between pt-2 px-3">
          <CollapseControl
            id={post.id}
            title="see more"
            className="ps-0 w-100 text-start"
          >
            <h5>
              <Burmese str={post.title} />
            </h5>
          </CollapseControl>

          <h5 className="d-flex align-items-center">
            <div
              className="me-3 btn dim"
              title="remove from favorite"
              onClick={props.onRemove}
            >
              <HeartFill className="text-danger" size={20} />
            </div>
            <CollapseControl
              id={post.id}
              title="see more"
              className=" dim d-flex align-items-center"
            >
              <Fullscreen size={20} />
            </CollapseControl>
          </h5>
        </FlexBox>
        <FlexBox className="justify-content-between">
          <h6 className="px-3">
            <Burmese str={post.originalWriter} />
          </h6>
          <div className="btn dim me-3" onClick={copyHandler}>
            <img
              src={icons[style.theme === "dark" ? "copy_white" : "copy_black"]}
              alt="copy"
              title="copy"
              style={{ width: "25px" }}
            />
          </div>
        </FlexBox>

        {toast && <ToastOnlyText str={toast} />}

        <Collapse id={post.id} className="position-relative">
          <audio
            controls
            style={{ width: "100%", maxWidth: "444px", height: "33px" }}
          >
            <source
              src={post.mp3
                .replace("/view?usp=sharing", "")
                .replace(
                  "https://drive.google.com/file/d/",
                  "https://docs.google.com/uc?export=open&id="
                )}
              type="audio/mp3"
            />
            <p>Your Browser does not support mp3.</p>
          </audio>

          <TextWrap id={"content" + props.id}>
            <Burmese str={post.content} />
          </TextWrap>
          <CollapseControl
            id={post.id}
            title="see less"
            className=" dim d-flex align-items-center position-absolute end-0 bottom-0 m-2 me-3"
          >
            <FullscreenExit size={20} />
          </CollapseControl>
        </Collapse>
      </CardTitle>
    </Card>
  );
}

export default FavoritePost;
