import { useEffect, useState } from "react";
import { ArrowUpSquare, ArrowUpSquareFill } from "react-bootstrap-icons";
import { useLocation } from "react-router";
import { Burmese } from "../../components/burmese converter/BurmeseConverter";
import { useStyle } from "../../components/context/StyleContext";
import {
  FlexBox,
  FullScreenArrowGoBack,
} from "../../components/layout/MyLayout";
import {
  LoadingSpinnerCenter,
  TextWrap,
  ToastOnlyText,
} from "../../components/utilities/Utilities";
import { icons } from "../../icons";
import { useDb } from "../home/database/DbContext";

function FullPost() {
  const { data } = useDb();
  const { style } = useStyle();
  const query = new URLSearchParams(useLocation().search);
  const id = query.get("name");
  const [feedback, setFeedback] = useState("");

  const [top, setTop] = useState("opacity-100");
  const [y, setY] = useState(0);

  const scrollHandler = () => {
    const curY = window.scrollY;
    curY > y ? setTop("opacity-0") : setTop("opacity-100");
    setY(curY);
  };

  useEffect(() => {
    window.addEventListener("scroll", scrollHandler);
    return () => window.removeEventListener("scroll", scrollHandler);
  }, [top, y]);

  const copyHandler = () => {
    let copy = document.getElementById("content");
    navigator.clipboard.writeText(copy.innerHTML);
    setFeedback("copied");
  };

  useEffect(() => {
    const timeOut = setTimeout(() => setFeedback(""), 1269);
    return () => clearTimeout(timeOut);
  }, [feedback]);

  return (
    <FullScreenArrowGoBack prompt={false} zu={true} mode={true}>
      {data ? (
        <div className="p-2">
          <FlexBox>
            <h5 className="ps-2" id="top">
              <Burmese str={data[id].title} />
            </h5>
            <div className="btn dim ms-2" onClick={copyHandler}>
              <img
                src={
                  icons[style.theme === "dark" ? "copy_white" : "copy_black"]
                }
                alt="copy"
                title="copy"
                style={{ width: "25px" }}
              />
            </div>
          </FlexBox>
          {data[id].mp3 && (
            <audio
              controls={true}
              loop={false}
              className={"position-sticky"}
              style={{
                top: "69px",
                width: "100%",
                maxWidth: "444px",
                height: "31px",
              }}
            >
              <source
                src={data[id].mp3
                  .replace("/view?usp=sharing", "")
                  .replace(
                    "https://drive.google.com/file/d/",
                    "https://docs.google.com/uc?export=open&id="
                  )}
                type="audio/mp3"
              />
              <p>Your browser does not support mp3 file.</p>
            </audio>
          )}
          {/* <div className="alert p-2 alert-warning">
            Audio file may take certain amount of time depends on article size
            or internet connection.
          </div> */}
          {feedback && <ToastOnlyText str={feedback} />}
          <TextWrap id="content">
            <Burmese str={data[id].content} />
          </TextWrap>

          <div
            className={
              "position-fixed bottom-0 end-0 translate-middle m-4 bg-primary " +
              top
            }
            onClick={() => window.scrollTo(0, 0)}
            title={"Back to top"}
            style={{ transition: "0.5s opacity" }}
          >
            <ArrowUpSquareFill size={31} />
          </div>
        </div>
      ) : (
        <LoadingSpinnerCenter className="" />
      )}
    </FullScreenArrowGoBack>
  );
}

export default FullPost;
