import { ArrowLeft, Plus, XLg } from "react-bootstrap-icons";
import { useStyle } from "../context/StyleContext";
import "../../mystyles/style.css";
import { useHistory } from "react-router";
import { useState } from "react";
import { Modal, ModalBody, ModalFooter } from "../utilities/Utilities";
import { ZUConverter } from "../burmese converter/BurmeseConverter";
import { Link } from "react-router-dom";
import { ThemeChangerIcon } from "../theme/Theme";

export function FullScreenArrowGoBack(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark" ? " bg-dark text-light " : " bg-light text-dark ";
  const history = useHistory();
  const [modal, setModal] = useState(false);

  const arrowLeft = () => {
    if (props.prompt) {
      setModal(true);
    } else {
      if (props.location) {
        history.push(props.location);
      } else {
        history.goBack();
      }
    }
  };

  return (
    <div className={props.className + theme} style={{ zIndex: "9998" }}>
      <nav
        className="nav-bar d-flex align-items-center text-light justify-content-between"
        style={{ background: props.navBg }}
      >
        <div className="btn dim" onClick={arrowLeft} title="Back">
          <ArrowLeft size={25} className="text-light" />
        </div>
        <div className="p-2 navbar-brand display-2 fw-normal ">
          {props.title}
        </div>
        <div className="d-flex">
          {props.zu && <ZUConverter className="me-3 btn btn-sm btn-success" />}
          {props.mode && (
            <div className="btn dim p-1 me-2">
              <ThemeChangerIcon width="29" />
            </div>
          )}
        </div>
      </nav>
      <div className="p-2">{props.children}</div>

      {props.prompt && modal && (
        <Modal
          title={props.modalTitle}
          onCancel={() => setModal(false)}
          mode={props.modalMode}
        >
          <ModalBody>{props.modalContent}</ModalBody>
          <ModalFooter>
            <button
              type="button"
              className="btn btn-secondary"
              onClick={() => setModal(false)}
            >
              {props.cancel}
            </button>
            <button
              type="button"
              className="btn btn-primary"
              onClick={() =>
                props.location ? history.push(props.location) : history.goBack()
              }
            >
              {props.okay}
            </button>
          </ModalFooter>
        </Modal>
      )}
    </div>
  );
}
FullScreenArrowGoBack.defaultProps = {
  navBg: "#000",
  prompt: false,
  cancel: "Close",
  zu: false,
  okay: "Okay",
  modalTitle: "Are you sure?",
  modalContent: "If you leave this page, you will lose current data.",
};

export function FullScreenXGoBack(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark" ? "bg-dark text-light " : "bg-light text-dark ";
  const history = useHistory();
  const [modal, setModal] = useState(false);

  const arrowLeft = () => {
    if (props.prompt) {
      setModal(true);
    } else {
      if (props.location) {
        history.push(props.location);
      } else {
        history.goBack();
      }
    }
  };

  return (
    <div
      className={
        "position-fixed top-0 end-0 bottom-0 start-0 overflow-auto " + theme
      }
      style={{ zIndex: "9998" }}
    >
      <nav
        className={
          "nav-bar d-flex align-items-center justify-content-between " +
          props.navBg
        }
      >
        <div className="p-2 ms-3 mb-0 h5 ">{props.title}</div>
        <button
          type="button"
          onClick={arrowLeft}
          className="btn d-flex p-2 me-1 border-0"
          style={{ boxShadow: "none" }}
        >
          <XLg className="align-self-center" />
        </button>
      </nav>

      <div className="p-2">{props.children}</div>

      {props.prompt && modal && (
        <Modal
          title={props.modalTitle}
          onCancel={() => setModal(false)}
          mode={props.modalMode}
        >
          <ModalBody>{props.modalContent}</ModalBody>
          <ModalFooter>
            <button
              type="button"
              className="btn btn-secondary"
              onClick={() => setModal(false)}
            >
              {props.cancel}
            </button>
            <button
              type="button"
              className="btn btn-primary"
              onClick={() =>
                props.location ? history.push(props.location) : history.goBack()
              }
            >
              {props.okay}
            </button>
          </ModalFooter>
        </Modal>
      )}
    </div>
  );
}
FullScreenXGoBack.defaultProps = {
  prompt: false,
  cancel: "Close",
  navBg: "bg-primary",
  okay: "Okay",
  modalTitle: "Are you sure?",
  modalContent: "If you leave this page, you will lose current data.",
};

export function FormContainer(props) {
  return (
    <div className="px-4 pt-3 m-auto" style={{ maxWidth: "600px" }}>
      {props.children}
    </div>
  );
}

//with theme div
export function Block(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark" ? " bg-dark text-light " : " bg-light text-dark";

  return <div className={props.className + theme}>{props.children}</div>;
}
//flex box with theme
export function FlexBox(props) {
  const { style } = useStyle();
  let theme =
    style.theme === "dark" ? " bg-dark text-light " : " bg-light text-dark";

  return (
    <div className={"d-flex align-items-center " + props.className + theme}>
      {props.children}
    </div>
  );
}

//plus icon in right lower corner
export function AddIconInRightLowerCorner(props) {
  return (
    <Link
      to={props.url}
      className={
        "position-fixed bottom-0 end-0 m-4 m-lg-5 bg-primary rounded-circle p-2 " +
        props.className
      }
    >
      <Plus className="text-light" size={37} />
    </Link>
  );
}
