import {
  Card,
  LoadingSpinnerCenter,
} from "../../components/utilities/Utilities";
import { useDb } from "../home/database/DbContext";
import { useFavorite } from "./FavContext";
import FavoritePost from "./FavoritePost";

function Favorite() {
  const { favorite, removeFavorite } = useFavorite();
  const { data } = useDb();

  return (
    <div className="p-2">
      {data ? (
        favorite.length ? (
          favorite.map((item) => {
            if (data[item]) {
              return (
                <FavoritePost
                  key={item}
                  id={item}
                  post={data[item]}
                  onRemove={() => removeFavorite(item)}
                />
              );
            } else {
              return <DeletedPost />;
            }
          })
        ) : (
          <h2 className="text-center opacity-50">No Favorite Post</h2>
        )
      ) : (
        <LoadingSpinnerCenter className="" />
      )}
    </div>
  );
}
export default Favorite;

const DeletedPost = () => {
  return (
    <Card>
      <h5 className="text-center p-1 text-danger">Post Deleted</h5>
    </Card>
  );
};
